import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import CApi from "@api";
import {
  CError,
  CTitle,
  CLoadingSpinner,
  CBarChartBranch,
  CPieChartStatus,
  CPieChartJenisBarang,
  CBarChartActiva,
  CBoxAsset,
  CBoxDepartement,
  CBoxTotalPerolehan,
  CBoxBranch,
  CMap,
} from "@components";
import { Row } from "antd";
import { FGetAccess } from "@function";

const Dashboard = () => {
  const router = useRouter();
  const [isLoading, setIsLoading] = useState(true);
  const [box, setBox] = useState({
    asset: 0,
    vendor: 0,
    branch: 0,
    totalPerolehan: 0.0,
  });
  const [status, setStatus] = useState([]);
  const [branch, setBranch] = useState([]);
  const [codeActiva, setCodeActiva] = useState([]);
  const [jenisBarang, setJenisBarang] = useState([]);
  const [maps, setMaps] = useState([]);
  const [accessList, setAccessList] = useState({
    viewAny: true,
    view: true,
    create: true,
    update: true,
    delete: true,
  });

  useEffect(() => {
    FGetAccess(router, setAccessList, setIsLoading);
    getData();
  }, []);

  const getData = async () => {
    isLoading
      ? false
      : await CApi({
          method: "GET",
          url: "api/dashboard",
        }).then((res) => {
          setBox(res.data.box);
          setBranch(res.data.branch);
          setStatus(res.data.status);
          setCodeActiva(res.data.codeActiva);
          setJenisBarang(res.data.jenisBarang);
          setMaps(res.data.map.data.features);
        });
  };

  if (isLoading) return <CLoadingSpinner />;
  if (!accessList.viewAny) return <CError status={403} />;

  return (
    <>
      <CTitle />
      <Row>
        <Row style={{ width: "100%" }} gutter={6}>
          <CBoxAsset data={box.asset} onClick={() => router.push("/asset")} />
          <CBoxDepartement data={box.vendor} />
          <CBoxTotalPerolehan data={box.totalPerolehan} />
          <CBoxBranch data={box.branch} />
        </Row>
        <Row style={{ marginTop: 10, width: "100%" }} gutter={6}>
          <CBarChartBranch data={branch} />
          <CPieChartStatus data={status} />
        </Row>
        <Row style={{ marginTop: 10, width: "100%" }} gutter={6}>
          <CBarChartActiva data={codeActiva} />
          <CPieChartJenisBarang data={jenisBarang} />
        </Row>
      </Row>
      <Row style={{ marginTop: 10 }}>
        <CMap data={maps} />
      </Row>
    </>
  );
};
export default Dashboard;
